const code = `type option('a) = Some('a) | None;
type language = Italian | English | Other;
type user = {
  username: string,
  name: option(string),
  lang: language,
};
let greeting = user => {
  let greetName =
    switch (user.name) {
    | Some(name) => name
    | None => "@" ++ user.username
    };
  switch (user.lang) {
  | Italian => "Ciao, " ++ greetName
  | English
  | Other => "Hi, " ++ greetName
  };
};
greeting({username: "illbexyz", name: None, lang: Italian}); /* Ciao, @illbexyz */

`

export default code
